@use "../base/variables" as v;
@use "../base/mixins" as m;

.contenedorGral {
	height: 100vh;
	width: 100%;
	background-color: #f7ede2;
	background-size: cover;
	top: 0;
	left: 0;
	z-index: -1;
	height: 100vh;
	width: 100%;
	align-content: center;
}
* {
	margin: 0;
	padding: 0;
}
.contenedorBooks {
	height: 100vh;
	display: inline;
	width: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	gap: 2rem;
	align-items: center;
	max-width: 120rem;
	margin: 0 auto;
	flex-wrap: wrap;
	flex-direction: row;
}
.logout {
	font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
	color: black;
}
@include m.telefono{
    .divFiltros {
        height: auto;
        width: auto;
    }
    .nav {
        display: flex;
    }
  }
