html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-padding-top: 0rem;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
}
body.fijar-body {
  overflow: hidden;
}
body.body-scroll {
  padding-top: 8.6rem;
}

p {
  font-size: 2rem;
  line-height: 1.5;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}
a:hover {
  cursor: pointer;
}

.a {
  text-decoration: none;
}
.a:hover {
  cursor: pointer;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.main {
  width: 90%;
  margin: 5rem auto;
}

.contSettings {
  width: 90%;
  margin: 0 auto;
}

.contSettings-info {
  width: 80%;
  margin: 0 auto;
}
.contSettings-info .enlace {
  margin: 0 auto;
  width: 80%;
}
.contSettings-info button {
  width: 100%;
  margin: 4rem auto;
}
.contSettings-info .contProfile {
  position: relative;
  width: 300px;
  height: 300px;
  background-size: cover;
}
.contSettings-info .contProfile img {
  height: 100%;
  object-fit: cover;
  object-position: center center;
}
.contSettings-info .contProfile .contFile {
  position: absolute;
  right: 0;
  bottom: 0rem;
  text-align: center;
}
.contSettings-info .contProfile .contFile label {
  margin: 0;
}
.contSettings-info #mifile {
  display: none;
  margin: 2rem auto;
}
.contSettings-info .labelmiinput {
  margin: 2rem auto;
  font-size: 14px;
  font-weight: bold;
  transition: all 0.5s;
  cursor: pointer;
  padding: 15px 40px !important;
  text-transform: uppercase;
  width: fit-content;
  text-align: center;
  display: block;
}

@media (min-width: 768px) {
  .contSettings-info {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1fr;
    align-items: center;
  }
}
.contenedorGral {
  height: 100vh;
  width: 100%;
  background-color: #f7ede2;
  background-size: cover;
  top: 0;
  left: 0;
  z-index: -1;
  height: 100vh;
  width: 100%;
  align-content: center;
}

* {
  margin: 0;
  padding: 0;
}

.contenedorBooks {
  height: 100vh;
  display: inline;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  align-items: center;
  max-width: 120rem;
  margin: 0 auto;
  flex-wrap: wrap;
  flex-direction: row;
}

.logout {
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande", "Lucida Sans", Arial, sans-serif;
  color: black;
}

@media (min-width: 320px) {
  .divFiltros {
    height: auto;
    width: auto;
  }
  .nav {
    display: flex;
  }
}